
import { defineComponent, ref } from 'vue';
import PhoneInput from '@/shared/components/PhoneInput/PhoneInput.vue';
import BreadCrumb from '@/shared/components/BreadCrumb/BreadCrumb.vue';
import SelectOption from '@/shared/components/SelectOption/SelectOption.vue';
import VueDatePicker from '@vuepic/vue-datepicker';
import '@vuepic/vue-datepicker/dist/main.css';
import type { COM21200113_RES, userRole } from "@/shared/type/COM21200113.type";
import type { CIB20100214_RES } from "@/shared/type/CIB20100214.type";
import type { subUserListDetail, CIB20200312_REQ_BODY, CIB20200312_RES, userInfo, userAccountList, userRoleList } from "@/shared/type/CIB20200312.type";
import type { CIB20200313_REQ_BODY, CIB20200313_RES } from "@/shared/type/CIB20200313.type";
import type { CIB20200321_REQ_BODY, CIB20200321_RES } from "@/shared/type/CIB20200321.type";
import type { CIB20200331_REQ_BODY, CIB20200331_RES } from "@/shared/type/CIB20200331.type";
import type { CIB20100332_REQ_BODY, CIB20100332_RES } from "@/shared/type/CIB20100332.type";
import { modalController } from '@ionic/vue';
import { APM20200113_RES } from "@/shared/type/APM20200113.type";
import { screenMode } from "@/shared/type/Approval.type";

export default defineComponent({
  name: 'cib202003sc01',
  components: {
    PhoneInput,
    BreadCrumb,
    SelectOption,
    VueDatePicker
  },
  data() {
    const viewType = this.$route.query.type;
    const customerNo = this.$route.query.customerNo;
    const userID = this.$route.query.userID;
    const customerName = this.$route.query.customerName;
    const managerBankID = this.$route.query.managerBankID;
    const cellPhoneNo = this.$route.query.cellPhoneNo;
    const companyName = this.$route.query.companyName;
    const masterUserStatus = this.$route.query.userStatus;
    const mainUserRoleID = this.$route.query.mainUserRoleID;
    const isEditable = ref<boolean>(false);
    const subUserPreWithdrawalAccount = [] as userAccountList[];
    const subUserdetail = ref<subUserListDetail>({
      userInfo: {} as userInfo,
      userRoleList: [] as userRoleList[],
      userAccountList: [] as userAccountList[]
    });
    const headers= [
      { columnName: this.$t("cib202003sc01.no") },
      { columnName: this.$t("cib202003sc01.account_no") },
      { columnName: this.$t("cib202003sc01.account_type") },
      { columnName: "View Only" },
      { columnName: "Full Access" }
    ];

    return {
      headers,
      userRole: [] as userRole[],
      serviceStatusCodeList: [] as { key: string, value: string }[],
      userAccountList: [] as { accountNo: string, accountType: string, designatedAccountTypeCode: string }[],
      subUserPreWithdrawalAccount,
      isbirthDate: '' as unknown,
      currentDate: new Date(),
      buttonAction: false,
      isInvalidInput: false,
      isDisableEdit: false,
      isApplicationCode: false,
      isMainUserRoleIDIsSTP: false,
      isInvalidUserIDInput: false,
      isWrongPhoneNumberFormat: false,
      isWrongEmailFormat: false,
      isInvalidAccountLength: false,
      isViewOnlyAccount: [] as boolean[],
      approvalRes: {} as any,
			operationMode: 'normal' as screenMode,
      viewOnly: "02",
      fullAccess: "01",
      subUserdetail,
      isEditable,
      viewType,
      customerNo,
      userID,
      managerBankID,
      customerName,
      cellPhoneNo,
      companyName,
      masterUserStatus,
      mainUserRoleID,
      startingNo:1,
      subUserRoleIDName: {
        userRoleID: '',
        userRoleName: '',
      },
    }
  },

  async mounted() {
    if(this.approvalType === ''){
      this.getSubUserRole();  // get userRole for register and update sub user
      this.handleGetServiceStatusCode();
      if (this.viewType == "edit") {
        this.getsubUserDetail();
      } else if (this.viewType == "register") {
        this.handleRegister();
      }
    }else{
      await this.$approval.getAuthorizationDetail().then(async (authDetail: APM20200113_RES) => {
				let authDetailParsed = JSON.parse(authDetail.body.requestContent);
				this.approvalRes = authDetail.body;
				this.operationMode = this.approvalType == 'my-request' ? 'read' : this.approvalType == 'my-approval' ? 'approval' : 'normal';

        this.subUserdetail.userInfo.userID = authDetailParsed.userID;
        this.subUserdetail.userInfo.cellPhoneNo = authDetailParsed.cellPhoneNo;
        this.subUserdetail.userInfo.customerNo = authDetailParsed.customerNo;
        this.subUserdetail.userInfo.customerName = authDetailParsed.customerName;
        this.subUserdetail.userInfo.departmentName = authDetailParsed.departmentName;
        this.subUserdetail.userInfo.email = authDetailParsed.email;
        this.subUserdetail.userInfo.firstName = authDetailParsed.firstName;
        this.subUserdetail.userInfo.jobTitleName = authDetailParsed.jobTitleName;
        this.subUserdetail.userInfo.lastName = authDetailParsed.lastName;
        this.subUserdetail.userInfo.managerBankID = authDetailParsed.managerBankID;
        this.subUserdetail.userInfo.companyName = authDetailParsed.managerBankName;
        this.subUserdetail.userInfo.serviceStatusCode = authDetailParsed.serviceStatusCode;
        this.subUserdetail.userInfo.birthDate = authDetailParsed.birthDate
        this.isbirthDate = this.$datetime.convertStringToDate(this.subUserdetail.userInfo.birthDate);
        this.serviceStatusCodeList.push({
          key: authDetailParsed.serviceStatusCode,
          value: this.$codeConverter.convertValue("ServiceStatusCode", authDetailParsed.serviceStatusCode),
        });

        authDetailParsed.accountList.map((item:any)=>{
          this.userAccountList.push({
              accountNo: item.accountNo,
              accountType: item.accountType,
              designatedAccountTypeCode: item.designatedAccountTypeCode,
          });
        });

        this.userAccountList.map((item:any)=>{
          this.subUserdetail.userAccountList.push({
            accountName: '',
            accountNo: item.accountNo,
            accountTypeCode: item.accountType,
            designatedAccountTypeCode: item.designatedAccountTypeCode,
            currencyCode: '',
            accountAccess: item.accountAccess
          });
  
          this.handleDesignatedAccountTypeCode(this.subUserdetail);
        });
      
        const resUserRole = await this.$request.getRequest("COM21200113", {}, false) as COM21200113_RES;
        let userRoleIDtemp= '';

        authDetailParsed.roleList.map((item:any)=>{
          userRoleIDtemp = item.userRoleID;
          return userRoleIDtemp;
        });
        console.log("userRoleIDtemp", userRoleIDtemp);

        this.userRole = resUserRole?.body?.list.filter((item:any)=>{
          return item.userRoleID == userRoleIDtemp;
        });
        console.log("this.userRole",this.userRole);

        this.userRole.map((item:any)=>{
          this.subUserRoleIDName.userRoleID = item.userRoleID;
          this.subUserRoleIDName.userRoleName = item.userRoleName;
        });
        console.log("this.subUserRoleIDName.userRoleID",this.subUserRoleIDName.userRoleID);         
        console.log("this.subUserRoleIDName.userRoleName ",this.subUserRoleIDName.userRoleName );         

        this.handleDesignatedAccountTypeCode(this.subUserdetail);
      })
		}
    },
  

  watch:{
    'subUserdetail.userInfo.email': function(emailValue){
      if(this.viewType == 'edit'){
        if(emailValue){
          this.isWrongEmailFormat = false;
        }
      }else if(this.viewType == 'register'){
        if(emailValue){
          // this.isWrongEmailFormat = this.$validate.validateEmail(this.subUserdetail.userInfo.email);
          this.isWrongEmailFormat = false;
        }
      }
    },

    'subUserdetail.userInfo.cellPhoneNo':function(newValue){
      if(newValue.length == 0){
        this.isWrongPhoneNumberFormat = false;
      }else if (newValue.length == 9 || newValue.length > 9){
        this.isWrongPhoneNumberFormat = false;
      }
      else if ( newValue.length < 9){
        this.isWrongPhoneNumberFormat = true;
      }
    }
  },

  computed:{
    subUserNO() {
      return (index: number, startingNo: number) => index + startingNo;
    },

    subUserRole() {
      return (roleID: string, rolename: string) => `${roleID}` +' : '+  `${rolename}`;
    },
    
    accountTypeCode() {
      return (accountTypeCode: string, accountTypename: string) => (`${accountTypeCode}`.length > 0) ? (`${accountTypeCode}` +' : '+  `${accountTypename}`) : '';
    },

    approvalType(): string {
      return this.$route.query.approvalType ? this.$route.query.approvalType.toString() : '';
    },

    formattedBirthDate(): string {
      return this.isbirthDate ? this.$datetime.formatVueDatePicker(this.isbirthDate as Date) : '';
    },
  },

  methods: {
    handleActionClick(actionType: string) {
      switch (actionType) {
        case "edit":
          // this.handleCheckViewOnlyAccount();
          this.isEditable = true;
          this.buttonAction = true;
          break;

        case "save":{
          let wrongEmailFormat = false;
          wrongEmailFormat = this.$validate.validateEmail(this.subUserdetail.userInfo.email);
          if (this.subUserdetail.userInfo.userID && wrongEmailFormat
            && this.subUserdetail.userInfo.firstName && this.subUserdetail.userInfo.lastName
            && this.subUserdetail.userInfo.cellPhoneNo && this.subUserdetail.userInfo.departmentName
            && this.subUserdetail.userInfo.serviceStatusCode && this.handleValidateUserAccountListAmount()
            && this.subUserdetail.userInfo.jobTitleName && this.isbirthDate
          ) {
            this.$dialog.onOpen({
              content: this.$t("message.would_you_like_to_save"),
              dialogMode: "Question",
              callback: () => {
                if (this.viewType == "edit") {
                  this.editSubUserInfo();
                } else {
                  this.registerSubUser();
                }
              }
            });
          } else {
            wrongEmailFormat ? this.isWrongEmailFormat = false : this.isWrongEmailFormat = true;
            this.isInvalidInput = true;  //check the required field
          }
        }
          break;

        case "cancel":
          this.$dialog.onOpen({
            content: this.$t("message.cancel_without_saving_changes"),
            dialogMode: "Question",
            callback: () => {
              if(this.viewType == 'register'){
                this.$router.back();
              }else if (this.viewType == 'edit'){
                this.getsubUserDetail();
                this.isEditable = false;
                this.buttonAction = false;
              }
              modalController.dismiss();
            }
          });
          break;

        case "backToList":
          this.backToSubUserList(); // back to sub user list (CIB202003SC00)
          break;
      }
    },

    async getSubUserRole(){
      const resUserRole = (await this.$request.getRequest("COM21200113", {}, false)) as COM21200113_RES;
      this.userRole = resUserRole?.body?.list.filter((item)=>{
        return item.userRoleID != 'STP' && item.userRoleID != 'FINAL';
      });
    },

    async handleGetServiceStatusCode(){
      this.serviceStatusCodeList = this.$store.state.CommonCodeModule.commonCode.ServiceStatusCode;
    },

    async getsubUserDetail() {
      const reqBody: CIB20200312_REQ_BODY = {
        customerNo: `${this.customerNo}`,
        userID: `${this.userID}`
      };

      const resUserDetail = (await this.$request.getRequest("CIB20200312", reqBody, false)) as CIB20200312_RES

      this.subUserdetail = resUserDetail?.body;
        this.subUserdetail.userRoleList.map((item: userRoleList) => {
        this.subUserRoleIDName.userRoleID = item.userRoleID;
        this.subUserRoleIDName.userRoleName = item.userRoleName;
      });

      let dateOfBirth = this.$datetime.convertStringToDate(this.subUserdetail.userInfo.birthDate)
      this.isbirthDate = dateOfBirth;

      if (this.subUserdetail.userInfo.serviceStatusCode == '00') {
        this.isApplicationCode = true;
        // this.isMainUserRoleIDIsSTP = true;
        if(this.mainUserRoleID == "STP"){
          this.isMainUserRoleIDIsSTP = true;
        }else{
          this.isMainUserRoleIDIsSTP = false;
        }
      }else{
        this.serviceStatusCodeList = this.$store.state.CommonCodeModule.commonCode.ServiceStatusCode.filter(item => item.key != '00');
      }
      // if masterUserStatus = ('00' or '01') it will allow user to edit sub user info but if not ('00' or '01') not allow user to edit.
      if(this.masterUserStatus ==  '00' || this.masterUserStatus == '01' || this.masterUserStatus == '08'){
        this.isDisableEdit = false;
      }else{
        this.isDisableEdit = true;
      }

      this.subUserdetail.userAccountList.map((item)=>{
        if(item.accountAccess == '02'){
          this.isViewOnlyAccount.push(true);
        }else{
          this.isViewOnlyAccount.push(false);
        }
      });

      this.subUserAccountTypeCode(this.subUserdetail);  // convert accountTypeCode to code:value
      this.handleDesignatedAccountTypeCode(this.subUserdetail);  // to check if user account is viewOnly or fullAccess
    },

    async handleRegister() {
      this.inputRegister();
      const reqBody: CIB20200313_REQ_BODY = {
        customerNo: `${this.customerNo}`,
        userID: `${this.managerBankID}` 
      };

      const resSubUserPreWithdrawalAccount = await this.$request.getRequest("CIB20200313", reqBody, false) as CIB20200313_RES;

      this.subUserPreWithdrawalAccount = resSubUserPreWithdrawalAccount?.body?.list;
      this.subUserdetail.userAccountList = this.subUserPreWithdrawalAccount;
      // .filter(item => item.designatedAccountTypeCode != '');

      this.subUserdetail.userAccountList.map((item)=>{
        if(item.designatedAccountTypeCode == '02'){
          this.isViewOnlyAccount.push(true);
        }else{
          this.isViewOnlyAccount.push(false);
        }
      });

      this.isbirthDate = '';
      this.subUserRoleIDName.userRoleID = '';
      this.subUserdetail.userInfo.userID = '';
      this.subUserdetail.userInfo.email = '';
      this.subUserdetail.userInfo.firstName = '';
      this.subUserdetail.userInfo.lastName = '';
      this.subUserdetail.userInfo.cellPhoneNo = '';
      this.subUserdetail.userInfo.birthDate = '';
      this.subUserdetail.userInfo.departmentName = '';
      this.subUserdetail.userInfo.jobTitleName = '';
      this.subUserdetail.userInfo.serviceStatusCode = "00";
      this.subUserdetail.userInfo.customerNo =`${this.customerNo}`;
      this.subUserdetail.userInfo.customerName = `${this.customerName}`;
      this.subUserdetail.userInfo.managerBankID = `${this.managerBankID}`;
      this.subUserdetail.userInfo.companyName = `${this.companyName}`;
      this.handleDesignatedAccountTypeCode(this.subUserdetail);  // to check if user account is viewOnly or fullAccess
      this.subUserAccountTypeCode(this.subUserdetail);  // convert accountTypeCode to code:value
      if(this.masterUserStatus ==  '00' && this.mainUserRoleID == 'STP' || this.masterUserStatus == '01' && this.mainUserRoleID == 'STP'){
        this.subUserRoleIDName.userRoleID = 'VIEW';
        this.isDisableEdit = false;
        this.isMainUserRoleIDIsSTP = true;
        this.subUserdetail.userInfo.serviceStatusCode = "00";
      }else{
        this.isDisableEdit = true;
      }
    },

    subUserAccountTypeCode(subUserAccount: subUserListDetail){
      const resData = subUserAccount.userAccountList.map((item: userAccountList)=>{
        item.accountTypeCode = this.accountTypeCode(item.accountTypeCode, this.$codeConverter.convertValue("AccountTypeCode", item.accountTypeCode))
        return item;
      });
      this.subUserdetail.userAccountList = resData;
    },

    handleDesignatedAccountTypeCode(subUserAccountList: subUserListDetail) {  // check if user account is viewOnly or fullAccess
      subUserAccountList.userAccountList.map((item: userAccountList, index: number) => {
        if (item.designatedAccountTypeCode == this.viewOnly) {  // this.viewOnly == 01
          this.subUserdetail.userAccountList[index].viewOnly = true;
          this.subUserdetail.userAccountList[index].fullAccess = false;
        } else if (item.designatedAccountTypeCode == this.fullAccess) {  // this.fullAccess == 03
          this.subUserdetail.userAccountList[index].fullAccess = true;
          this.subUserdetail.userAccountList[index].viewOnly = false;
        } else {
          this.subUserdetail.userAccountList[index].fullAccess = false;
          this.subUserdetail.userAccountList[index].viewOnly = false;
        }
        return item;
      });
    },

    async registerSubUser() {
      // this.subUserdetail.userAccountList.map((item: userAccountList) => {
      //   this.userAccountList.push({
      //     accountNo: item.accountNo,
      //     accountType: item.accountTypeCode,
      //     designatedAccountTypeCode: item.designatedAccountTypeCode
      //   })
      // });

      const reqBodyRegister: CIB20200321_REQ_BODY = {
        accountList: this.userAccountList,
        cellPhoneNo: this.subUserdetail.userInfo.cellPhoneNo,
        customerName: this.subUserdetail.userInfo.customerName,
        customerNo: this.subUserdetail.userInfo.customerNo,
        departmentName: this.subUserdetail.userInfo.departmentName,
        email: this.subUserdetail.userInfo.email,
        jobTitleName: this.subUserdetail.userInfo.jobTitleName,
        managerBankID: this.subUserdetail.userInfo.managerBankID,
        birthDate: this.isbirthDate? this.$datetime.convertDateToString(this.isbirthDate as Date) : '',
        managerBankName: this.subUserdetail.userInfo.companyName,
        roleList: [{ userRoleID: this.subUserRoleIDName.userRoleID }],
        serviceStatusCode: this.subUserdetail.userInfo.serviceStatusCode,
        userID: this.subUserdetail.userInfo.userID,
        firstName: this.subUserdetail.userInfo.firstName,
        lastName: this.subUserdetail.userInfo.lastName
      };

      const registerResponse = (await this.$request.getRequest("CIB20200321", reqBodyRegister, false)) as CIB20200321_RES;
  
      this.userAccountList = [];  //  for prevent when push userAccountList while (userID,firstName,lastName,email... == ''), just set it to [] and when click register again it will receive new value.
      this.inputRegister();
      this.backToSubUserList();  // back to sub user list (CIB202003SC00)
      
      if (registerResponse.header.result == true) {
        this.inputRegister();
        this.backToSubUserList();  // back to sub user list (CIB202003SC00)
        this.$dialog.onOpen({
          content: `${this.$t("message.register_success")}`,
          dialogMode: "Success",
          callback: () => {
            modalController.dismiss()
          }
        });
      } else {
        console.log(registerResponse.header.error_text);
      }
    },

    async editSubUserInfo() {
      // this.subUserdetail.userAccountList.map((item: userAccountList) => {
      //   this.userAccountList.push({
      //     accountNo: item.accountNo,
      //     accountType: item.accountTypeCode,
      //     designatedAccountTypeCode: item.designatedAccountTypeCode
      //   })
      // });

      const reqBodyUpdate: CIB20200331_REQ_BODY = {
        accountList: this.userAccountList, //account as array
        cellPhoneNo: this.subUserdetail.userInfo.cellPhoneNo,
        customerName: this.subUserdetail.userInfo.customerName,
        customerNo: this.subUserdetail.userInfo.customerNo,
        departmentName: this.subUserdetail.userInfo.departmentName,
        email: this.subUserdetail.userInfo.email,
        jobTitleName: this.subUserdetail.userInfo.jobTitleName,
        managerBankID: this.subUserdetail.userInfo.managerBankID,
        managerBankName: this.subUserdetail.userInfo.companyName,
        birthDate: this.$datetime.convertDateToString(this.isbirthDate as Date),
        roleList: [{ userRoleID: this.subUserRoleIDName.userRoleID }], // array
        serviceStatusCode: this.subUserdetail.userInfo.serviceStatusCode,
        userID: this.subUserdetail.userInfo.userID,
        firstName: this.subUserdetail.userInfo.firstName,
        lastName: this.subUserdetail.userInfo.lastName
      };

      const updateSubUserInfo = (await this.$request.getRequest("CIB20200331", reqBodyUpdate, false)) as CIB20200331_RES;

      this.inputUpdate();

      if (updateSubUserInfo.header.result) {
        this.getsubUserDetail();
        this.inputUpdate();
        this.$dialog.onOpen({
          content: `${this.$t("message.update_success")}`,
          dialogMode: "Success",
          callback: () => {
            modalController.dismiss()
          }
        });
      } else {
        console.log(updateSubUserInfo.header.error_text);
      }
    },

    handleValidateUserAccountListAmount(): boolean{
      let isValidUserAccountList = false;
      this.isInvalidAccountLength = false;
      this.userAccountList = []
      this.subUserdetail.userAccountList.map((item: userAccountList) => {
        this.userAccountList.push({
            accountNo: item.accountNo,
            accountType: item.accountTypeCode,
            designatedAccountTypeCode: item.designatedAccountTypeCode
        })
        if(item.viewOnly || item.fullAccess){
          isValidUserAccountList = true;
        }else if(!item.viewOnly && !item.fullAccess && isValidUserAccountList){
          isValidUserAccountList = true;
        }
        else if(!item.viewOnly && !item.fullAccess && !isValidUserAccountList){
          isValidUserAccountList = false;
        }
      });

      if(!isValidUserAccountList){
        this.isInvalidAccountLength = true;
      }
      return isValidUserAccountList;
    },

    // handleCheckViewOnlyAccount(){
    //   this.subUserdetail.userAccountList.map((item)=>{
    //     item.designatedAccountTypeCode == '01' ? this.isViewOnlyAccount.push(true) : this.isViewOnlyAccount.push(false)
    //   })
    // },

    async handleGenerateUserID() {
      const generateUserID = (await this.$request.getRequest("CIB20100214", {} , false)) as CIB20100214_RES
      this.subUserdetail.userInfo.userID = generateUserID.body.userID;
      // if(generateUserID.header.result){
      //   this.$dialog.onOpen({
      //     content: this.$t("message.success"),
      //     dialogMode:"Success",
      //     callback: () => {
      //       modalController.dismiss();
      //     }
      //   });
      // }
    },

    async resetPasswordViaEmail() {
      const reqBody: CIB20100332_REQ_BODY = {
        customerNo: this.subUserdetail.userInfo.customerNo,
        userID: this.subUserdetail.userInfo.userID,
        email: this.subUserdetail.userInfo.email
      };

      const responseData = (await this.$request.getRequest("CIB20200332", reqBody , false)) as CIB20100332_RES;
      
      if(responseData.header.result == true){
        this.$dialog.onOpen({
          content: this.$t("message.reset_password_success"),
          dialogMode:"Success",
          callback: () => {
            modalController.dismiss();
          }
        });
      }
    },

    onChangeViewOnly(index: number) {  // if checkbox doesn't check the value = false
      if (this.subUserdetail.userAccountList[index].viewOnly == true) {  // if checked the value = true
        this.subUserdetail.userAccountList[index].viewOnly = true;  // set viewOnly checkbox to checked
        this.subUserdetail.userAccountList[index].designatedAccountTypeCode = this.viewOnly;  // set the value = "01" (designatedAccountTypeCode = 01) 
        this.subUserdetail.userAccountList[index].fullAccess = false;
      }
      else if (this.subUserdetail.userAccountList[index].fullAccess == false && this.subUserdetail.userAccountList[index].viewOnly == false) {  // if fullAccess = false and viewOnly also false the value will be '' or 00
        this.subUserdetail.userAccountList[index].designatedAccountTypeCode = ''; // set the value to 00
      }
    },

    onChangeFullAccess(index: number) {
      if (this.subUserdetail.userAccountList[index].fullAccess == true) {
        this.subUserdetail.userAccountList[index].fullAccess = true;
        this.subUserdetail.userAccountList[index].designatedAccountTypeCode = this.fullAccess;  // this.fullAccess == 03
        this.subUserdetail.userAccountList[index].viewOnly = false;
      } else if (this.subUserdetail.userAccountList[index].viewOnly == false && this.subUserdetail.userAccountList[index].fullAccess == false) {
        this.subUserdetail.userAccountList[index].designatedAccountTypeCode = '';
      }
    },

    inputUpdate() {
      this.isEditable = false;
      this.buttonAction = false;
    },

    inputRegister() {
      this.isEditable = true;
      this.buttonAction = true;
    },

    backToSubUserList() {
      this.$router.back();  // back to sub user list (CIB202003SC00)
    } 
  }
})
